/* Aside styles */

.aside {
  min-width: 280px;
}

/* Header styles */

.user__avatar {
  width: 50px;
  height: 50px;
}

/* Overview styles */

.overview__current {
  width: 365px;
  min-height: 176px;
  border-radius: 10px 0 0 10px;
}

.overview__hold {
  border-radius: 10px;
  margin-left: -10px;
}

.divider {
  margin: 18px 0;
}

.overview__amount {
  font-family: 'Space Grotesk', sans-serif;
  line-height: 43px;
}

/* Table styles */

.table__search {
  height: 42px;
  width: 300px;
  padding: 11px 15px 11px 15px;
}

.search__icon {
  transform: translate(-50%, -50%);
}

.input__pickdate, .table__payout__btn{
  height: 42px;
}

/* .table__heading {

  } */

.table__data {
  box-sizing: border-box;
  border: 1px solid #FFFFFF;
  border-bottom-color: #F5F5F5;
}

.table__data:hover {
  box-shadow: 0px 2px 30px rgba(0, 0, 0, 0.08);
  border-color: #2C665D;
  border: 1px solid;
  border-radius: 5px;
}

.table::-webkit-scrollbar {
  display: none !important;
}

@media screen and (max-width: 575px){
  .overview__current {
    border-radius: 10px 10px 0 0;
    width: 100%;
  }

  .overview__hold {
    border-radius: 0 0 10px 10px;
    margin-left: 0px;
    margin-top: -10px;
    min-width: 100%;
    max-width: 100%;
  }
}

